<template>
  <div id="wrap" :style="{ height: screenHeight-69 + 'px' }">
    <div id="main" :style="{ top: nowTop + 'px' }">
      <ul id="pageUl" type="circle">
        <li  id="pageUlLi1" @click="navTo(1,$event)" class="pageUlLi" :class="{'active': curIndex == 1}"></li>
        <li id="pageUlLi2" @click="navTo(2,$event)" class="pageUlLi" :class="{'active': curIndex == 2}"></li>
        <li id="pageUlLi3" @click="navTo(3,$event)" class="pageUlLi" :class="{'active': curIndex == 3}"></li>
        <li id="pageUlLi4" @click="navTo(4,$event)" class="pageUlLi" :class="{'active': curIndex == 4}"></li>
        <li id="pageUlLi5" @click="navTo(5,$event)" class="pageUlLi" :class="{'active': curIndex == 5}"></li>
      </ul>
      <div id="page1" class="page">
        <img src="@a/img/about/a1.png" alt="">
        <div class="title">
          <p>时间没有等我，是你忘了带我走，我左手过目不忘的的萤火，右手里是十年一个漫长的打坐。</p>
        </div>
<!--        <p>like-明</p>-->
      </div>
      <div id="page2" class="page">
        <img src="@a/img/about/a2.png" alt="">
      </div>
      <div id="page3" class="page">
        <img src="@a/img/about/a3.png" alt="">
      </div>
      <div id="page4" class="page">
        <img src="@a/img/about/a4.png" alt="">
      </div>
      <div id="page5" class="page">
        <img src="@a/img/about/a5.png" alt="">
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Scorll',
  data(){
    return{
      screenWeight: 0,    // 屏幕宽度
      screenHeight: 0,    // 屏幕高度
      index: 1,        // 用于判断翻页
      curIndex: 1,      // 当前页的index
      startTime: 0,      // 翻屏起始时间
      endTime: 0,       // 上一次翻屏结束时间
      nowTop: 0,       // 翻屏后top的位置
      pageNum: 0,       // 一共有多少页
      main: Object,
      obj: Object
    }
  },
  mounted(){
    this.screenWeight = document.documentElement.clientWidth;
    this.screenHeight = document.documentElement.clientHeight;
    this.main = document.getElementById("main");
    this.obj = document.getElementsByTagName("div");
    for (let i = 0; i < this.obj.length; i++) {
      if (this.obj[i].className == 'page') {
        this.obj[i].style.height = this.screenHeight + "px";
      }
    }
    this.pageNum = document.querySelectorAll(".page").length;

    // 浏览器兼容
    if ((navigator.userAgent.toLowerCase().indexOf("firefox") != -1)) {
      document.addEventListener("DOMMouseScroll", this.scrollFun, false);
    } else if (document.addEventListener) {
      document.addEventListener("mousewheel", this.scrollFun, false);
    } else if (document.attachEvent) {
      document.attachEvent("onmousewheel", this.scrollFun);
    } else {
      document.onmousewheel = this.scrollFun;
    }
  },
  methods: {
    scrollFun(event) {
      this.startTime = new Date().getTime();
      // mousewheel事件中的 “event.wheelDelta” 属性值：返回的如果是正值说明滚轮是向上滚动
      // DOMMouseScroll事件中的 “event.detail” 属性值：返回的如果是负值说明滚轮是向上滚动
      let delta = event.detail || (-event.wheelDelta);
      // 如果当前滚动开始时间和上次滚动结束时间的差值小于1.5s，则不执行翻页动作，这样做是为了实现类似节流的效果
      if ((this.startTime - this.endTime) > 1500) {
        if (delta > 0 && parseInt(this.main.offsetTop) >= -(this.screenHeight * (this.pageNum - 2))) {
          // 向下滚动
          this.index++;
          this.toPage(this.index);
        } else if (delta < 0 && parseInt(this.main.offsetTop) < 0) {
          // 向上滚动
          this.index--;
          this.toPage(this.index);
        }
        // 本次翻页结束，记录结束时间，用于下次判断
        this.endTime = new Date().getTime();
      }
    },
    // 翻页
    toPage(index) {
      if (index != this.curIndex) {
        let delta = index - this.curIndex;
        this.nowTop = this.nowTop - delta * this.screenHeight;
        this.curIndex = index;
      }
    },
    //点击翻页
    navTo(a) {
      this.index = a;
      this.toPage(a);
      this.active = a;
    }
  }
  }
</script>
<style scoped>
