/*}*/
.title {
  position: absolute;
  right: 100px;
  top: 200px;
  width: 300px;
  color: #7f7f8e;
  font-size: 18px;
  font-family: 微软雅黑;
  font-style: italic;
}
@keyframes fadeInAnimation {
  0% {
    opacity: 0; /*设置不透明度*/
  }
  100% {
    opacity: 1;
  }
}

html, body {
  height: 100%;
}

body, ul, li, a, p, div {
  /*慎删*/
  padding: 0px;
  margin: 0px;
}

#wrap {
  overflow: hidden;
  width: 100%;
}

#main {
  position: relative;
  transition:top 1.5s;
}

.page {
  /*谨删*/
  width: 100%;
  margin: 0;
}

#pageUl {
  position: fixed;
  right: 10px;
  bottom: 50%;
}

.active{
  color: red;
}
img {
  width: 800px;
}
</style>